@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");
@import url("https://fonts.cdnfonts.com/css/roboto");
@import url("https://fonts.cdnfonts.com/css/inter");

* {
    font-family: "Inter", sans-serif;
    font-style: inherit;
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style-type: none;
    max-width: 100%;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
}

*:focus {
    outline: none !important;
}

* a {
    color: inherit;
}

* input::-webkit-outer-spin-button, * input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

* input[type=search]::-ms-clear {
    display: none;
    width: 0;
    height: 0;
}

* input[type=search]::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
}

* input[type=search]::-webkit-search-decoration,
* input[type=search]::-webkit-search-cancel-button,
* input[type=search]::-webkit-search-results-button,
* input[type=search]::-webkit-search-results-decoration {
    display: none;
}

svg {
    min-width: 4px;
    min-height: 4px;
}

svg path {
    fill: child;
}

.col {
    display: flex;
    flex-direction: column;
}

.row {
    display: flex;
    flex-direction: row;
}

@media (max-width: 950px) {
    .row {
        flex-direction: column;
    }
}

.amount {
    cursor: pointer;
    margin: 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
    border-radius: 10px;
    border: 0;
    width: 34px;
    height: 23px;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: #3F4E65;
}

.amount__value {
    display: flex;
    align-items: center;
    justify-content: center;
}

.input {
    display: flex;
}

.input input {
    width: 263px;
    height: 50px;
    display: block;
    padding: 9px 45px 9px 20px;
    border: 0;
    border-radius: 50px;
    background-color: #E5E5E5;
}

.input input::placeholder {
    color: #bbb;
}

.input button {
    height: 100%;
    border: 0;
    border-radius: 50px;
    position: relative;
    background-color: rgba(255, 200, 94, 0.96);
}

img {
    min-height: 50px !important;
    min-width: 50px !important;
    overflow: scroll;
}

.btn__text {
    color: #FFFFFF;
    font-size: 0.8em;
    cursor: pointer;
    font-weight: 700;
    transition: all 0.2s;
    white-space: nowrap;
    padding: 10px;
    border-radius: 80px;
    background: rgba(255, 200, 94, 0.96);
    border: 0;
    display: flex;
    align-items: center;
}

.btn__text span {
    color: #FFFFFF;
    margin: 10px;
}

.btn__text:hover {
    background: rgba(255, 208, 94, 0.96);
}

.btn__text:hover svg {
    transform: translateX(5px);
}

.btn__text svg {
    min-width: 15px;
    min-height: 15px;
    margin: 10px;
    transition: transform 0.3s ease-in-out;
}

.btn__text:active {
    transform: scale(0.95);
}

.btn__img {
    margin: 10px;
    padding: 10px;
    cursor: pointer !important;
    border: 0;
    border-radius: 50%;
    display: flex;
    background-color: rgba(255, 200, 94, 0.96);
    height: 39px;
    width: 39px;
    align-items: center;
    justify-content: center;
}

.btn__img img {
    min-height: 12px;
    min-width: 12px;
}

.btn__cart {
    cursor: pointer;
    display: flex;
    border: 0;
    background-color: #FFFFFF;
}

.btn__indicator {
    border-radius: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 200, 94, 0.96);
    color: #FFFFFF;
    position: relative;
    margin-top: -7px;
    margin-left: -12px;
    padding: 12px;
    width: 10px;
    height: 10px;
}

.breadcrumbs {
    display: flex;
    flex-direction: row;
    margin: 20px 0;
}

.breadcrumbs .vl {
    height: 15px;
    margin: 0 13px;
}

.breadcrumbs .breadcrumb {
    font-weight: 300;
    font-size: 0.8rem;
    line-height: 120%;
    color: #3F4E65;
}

.breadcrumbs .active {
    color: rgba(63, 78, 101, 0.5);
}

@media (max-width: 950px) {
    .breadcrumbs .breadcrumb {
        display: flex;
        padding: 10px;
        align-items: center;
    }

    .breadcrumbs .breadcrumb span {
        font-weight: 600;
        font-size: 1rem;
        line-height: 120%;
        margin-left: 10px;
    }

    .breadcrumbs .breadcrumb .arrow {
        display: flex;
        padding: 5px;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: rgba(255, 200, 94, 0.2);
    }

    .breadcrumbs .breadcrumb .arrow svg {
        width: 10px;
        height: 10px;
    }

    .breadcrumbs .breadcrumb .arrow svg path {
        fill: #3F4E65;
    }
}

.vl {
    display: flex;
    margin-top: 7px;
    border-left: 2px dotted rgba(63, 78, 101, 0.1);
    height: 30px;
}

.hl {
    display: flex;
    margin-top: 7px;
    border-top: 2px dotted rgba(63, 78, 101, 0.1);
    width: 80%;
}

.mobile {
    display: none;
}

@media (max-width: 950px) {
    .mobile {
        display: inherit;
    }
}

.pc {
    display: inherit;
}

@media (max-width: 950px) {
    .pc {
        display: none;
    }
}

.modal {
    padding: 25px;
    box-shadow: 0 0 20px 2000px rgba(0, 0, 0, 0.75);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 5% 10%;
    height: 80%;
    width: 80%;
    border-radius: 8px;
    border: 0;
    background-color: white;
    z-index: 2;
    flex-direction: column;
}

.modal .btn__close {
    cursor: pointer;
    border: 0;
    border-radius: 50%;
    display: flex;
    background-color: inherit;
    height: 39px;
    width: 39px;
    align-items: center;
    justify-content: center;
}

.modal .btn__close svg {
    height: 25px;
    width: 25px;
}

.modal .btn__close svg path {
    fill: rgba(255, 200, 94, 0.96) !important;
}

.modal .btn__img {
    width: 60px;
    height: 60px;
}

.modal__top {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    min-height: 10%;
    margin-bottom: 15%;
}

.modal__content {
    text-align: center;
    font-style: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.modal__content h2 {
    font-weight: 500;
    font-size: 2em;
    text-transform: uppercase;
    line-height: 120%;
}

.modal__content p {
    font-style: normal;
    font-weight: 300;
    font-size: 2em;
    line-height: 120%;
    color: #3F4E65;
}

@media (max-width: 950px) {
    .modal__content {
        text-align: center;
        font-style: normal;
    }

    .modal__content h2 {
        font-size: 2em;
        text-transform: uppercase;
        line-height: 120%;
    }

    .modal__content p {
        font-style: normal;
        font-weight: 300;
        font-size: 1em;
        line-height: 120%;
    }
}


